  width: 100px;
  height: 100px;
  -webkit-perspective: 780px;
          perspective: 780px;
}
.viewport-inner {
  position: absolute;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  border-radius: 50%;
  -o-border-radius: 50%;
  -ms-border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
}
.viewport-inner.viewport-one {
    left: 0%;
    top: 0%;
    animation: viewport-rotate-one 1.15s linear infinite;
    -o-animation: viewport-rotate-one 1.15s linear infinite;
    -ms-animation: viewport-rotate-one 1.15s linear infinite;
    -webkit-animation: viewport-rotate-one 1.15s linear infinite;
    -moz-animation: viewport-rotate-one 1.15s linear infinite;
    border-bottom: 3px solid #5C5EDC;
}
.viewport-inner.viewport-two {
    right: 0%;
    top: 0%;
    animation: viewport-rotate-two 1.15s linear infinite;
    -o-animation: viewport-rotate-two 1.15s linear infinite;
    -ms-animation: viewport-rotate-two 1.15s linear infinite;
    -webkit-animation: viewport-rotate-two 1.15s linear infinite;
    -moz-animation: viewport-rotate-two 1.15s linear infinite;
    border-right: 3px solid rgba(76, 70, 101, 0.99);
}
.viewport-inner.viewport-three {
    right: 0%;
    bottom: 0%;
    animation: viewport-rotate-three 1.15s linear infinite;
    -o-animation: viewport-rotate-three 1.15s linear infinite;
    -ms-animation: viewport-rotate-three 1.15s linear infinite;
    -webkit-animation: viewport-rotate-three 1.15s linear infinite;
    -moz-animation: viewport-rotate-three 1.15s linear infinite;
    border-top: 3px solid #e9908a;
}
@keyframes viewport-rotate-one {
0% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
            transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
}
100% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
            transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
}
}
@-webkit-keyframes viewport-rotate-one {
0% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
}
100% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
}
}
