.bubble .circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #36b;
  -webkit-animation: pulse 1.5s infinite;
          animation: pulse 1.5s infinite;
  -webkit-box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
